.create-acc {
  
  &__window {
    max-width: 540px;
    padding-top: 60px;
  }

  &__title {
    margin-bottom: 12px;
  }

  &__confirm {
    display: flex;
    align-items: flex-start;
    margin-bottom: 40px;

    &-input {
      width: 20px;
      height: 20px;
      margin-right: 8px;
      margin-top: 5px;
    }

    &-checkbox {
      width: 20px;
      height: 20px;
    }
  }

  &__sign {
    margin-bottom: 40px;
  }
}