.card {
  display: flex;
  margin-bottom: 16px;
  

  &__item {
    margin-right: 16px;
    padding-top: 60px;

    background-color: var(--color-white);
    box-shadow: 0px 12px 28px rgba(244, 245, 245, 0.2);
    border-radius: 32px;

    &:last-child {
      margin-right: 0;
    }
  }

  &__title {
    font: var(--heading-2);
    color: #0f0f0f;

    text-align: center;
    margin-bottom: 32px;
  }

  &__inner {
    padding: 32px 40px 40px;

    border-top: 1px solid #dbdbdb;
  }

  &__func {
    min-height: 212px;

    &-item {
      display: flex;
      align-items: flex-start;
      margin-bottom: 16px;

      &:last-child {
        margin-bottom: 0;
      }
    }

    &-icon {
      margin-right: 12px;
    }

    &-descr {
      max-width: 263px;
      font: var(--paragraph);
      color: #5c5c5c;
    }
  }

  &__bottom {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  &__price {
    display: flex;
    align-items: flex-start;
    margin-bottom: 16px;

    &-currency {
      font-weight: 700;
      font-size: 26px;

      display: inline-block;
      margin-right: 4px;
      margin-top: 10px;
    }

    &-cost {
      display: inline-block;
      font-weight: 700;
      font-size: 50px;
    }
  }

  &__started {
    font: var(--button);
    color: var(--color-white);

    width: 100%;
    padding: 16px 30px;

    background-color: var(--color-black-1);
    border-radius: 12px;
    transition: background-color .2s ease-in-out, color .2s ease-in-out;

    &:hover {
      transition: background-color .2s ease-in-out, color .2s ease-in-out;
      background-color: var(--color-blue);
      color: var(--color-white);
    }
  }
}

.card-tab {
  display: none;
}

.card-tab--active {
  display: flex;
}


// media

@media (max-width: 968px) {
  .card {
    flex-direction: column;

    &__item {
      margin-right: 0;
      margin-bottom: 16px;

      &:last-child {
        margin-bottom: 0;
      }
    }

    &__price {
      align-items: flex-end;

      &-cost {
        font-size: 30px;
      }
    }
  }
}