.steps {
  padding: var(--main-offset) 0;
}

.stage {
  margin-bottom: 20px;
  
  &__item {
    align-items: flex-end;
    width: 100%;
    max-width: 388px;
    min-height: 300px;
    margin-right: 16px;
    padding: 6px 6px 40px 40px;

    position: relative;

    background-color: var(--color-white);
    box-shadow: 0px 12px 28px rgba(244, 245, 245, 0.2);
    border-radius: 32px;

    &:last-child {
      margin-right: 0;
    }

    &-info {
      margin-right: 26px;
    }
  }

  &__illustration {
    position: absolute;
    right: 6px;
    top: 6px;
  }

  &__title {
    font: var(--heading-4);
    color: var(--color-black-2);

    margin-bottom: 26px;
  }

  &__text {
    display: inline-block;
    font: var(--paragraph-blog);
    color: var(--color-black-1);

    max-width: 227px;
  }
}

.invite {
  align-items: center;
  justify-content: space-between;
  padding: 40px 80px;
  background-color: var(--color-blue);
  box-shadow: 0px 4px 24px rgba(213, 216, 221, 0.2);
  border-radius: 32px;

  &__stock {

    &-text {
      font: var(--paragraph);
      color: var(--color-white);
      max-width: 508px;
      margin-right: 20px;
    }
  }

  &__title {
    font: var(--heading-3);
    color: var(--color-white);

    margin-bottom: 24px;
  }

  &__over {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    max-width: 445px;
    padding: 28px 72px;

    background-color:#f1f4f8;
    border-radius: 16px;

    &-request {
      font: var(--heading-5);
      color: var(--color-black-2);

      margin-bottom: 10px;
    }
  }

  &__bottom {
    display: flex;
    align-items: flex-end;
  }

  &__link {
    font: var(--heading-5);
    color: var(--color-blue);

    text-decoration: underline;
    margin-right: 8px;

    &:hover {
      text-decoration: none;
    }
  }

  &__copy {
    display: inline-block;
    width: 24px;
    height: 24px;
    
    background-image: url('../img/copy-icon.svg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: 18px 20px;
  }
}


// media

@media (max-width: 968px) {
  .stage {

    &__illustration {
      width: 110px;
      height: 110px;
    }
  }

  .invite {
    padding: 32px;

    &__over {
      max-width: 336px;
      padding: 20px 10px;
    }
  }
}

@media (max-width: 768px) {
  .stage {
    flex-direction: column;

    &__item {
      max-width: none;
      min-height: 186px;
      margin-right: 0;
      margin-bottom: 16px;

      &:last-child {
        margin-bottom: 0;
      }
    }

    &__illustration {
      width: 110px;
      height: 110px;
    }

    &__text {
      max-width: none;
    }
  }

  .invite {
    flex-direction: column;
    padding: 20px;

    &__stock {
      margin-bottom: 32px;
    }

    &__over {
      max-width: none;
    }
  }
}

@media (max-width: 375px) {
  .stage {

    &__illustration {
      width: 93px;
      height: 93px;
    }
  }

  .invite {

    &__link {
      font-size: 14px;
    }
  }
}