.modal-payment {
  opacity: 0;
 
  &__window {
    max-width: 580px;
  }

  &__way {
    display: flex;
    margin-bottom: 40px;

    &-btn {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      width: 240px;
      margin-right: 20px;
      padding: 13px 0; 

      background-color: var(--color-black);
      border-radius: 4px;

      &:last-child {
        margin-right: 0;
      }
    }
  }

  &__form {

    &-label {
      color: #070033;
    }
  }

  &__card {

    &-field {
      max-width: 230px;
      margin-right: 20px;

      &:last-child {
        margin-right: 0;
      }
    }
  }

  &__pay {

    &-card {
      margin-bottom: 12px;
    }
  }

  &__info {
    font: var(--paragraph);
    color: #7f7f7f;

    margin-bottom: 60px;
  }

  &__crypto {

    &-title {
      font-weight: 700;
      font-size: 20px;
      color: var(--color-black-2);

      margin-bottom: 32px;
    }
  }
}