html {
  box-sizing: border-box;
  // overflow-y: scroll;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: var(--font-family);
}

body.scroll-lock {
  overflow: hidden;
  padding-right: 17px;
}

img {
  display: block;
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden; // если используете на сайте position: sticky - уберите эту настройку
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}


.main {
  background-color: var(--color-water);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.flex {
  display: flex;
}

.section-title {
  font: var(--heading-2);
  color: var(--color-black-2);
  text-align: center;

  margin-bottom: 37px;
}

.page-title {
  font: var(--heading-1);
  color: var(--color-black-2);

  margin-bottom: 20px;
}

.page-subtitle {
  font: var(--paragraph-blog);
  color: var(--color-black-1);
  
  margin-bottom: 52px;
}

.btn-hover {
  transition: background-color .2s ease-in-out, color .2s ease-in-out, border .1s linear;
  
  &:hover {
    transition: background-color .2s ease-in-out, color .2s ease-in-out, border .1s linear;
    background-color: transparent;
    border: 1px solid var(--color-blue);
    color: var(--color-black-2);
  }

  &--primary {

    &:hover {
      transition: background-color .2s ease-in-out, color .2s ease-in-out, border .1s linear;
      background-color: transparent;
      border: 1px solid var(--color-black);
      color: var(--color-black-2);
    }
  }
}

.period {
  display: flex;
  align-items: center;
  margin-bottom: 48px;

  &__meaning {
    font: var(--paragraph-blog);
    color: var(--color-white);
    padding: 17px 45px;

    background-color: var(--color-blue);

    &--small {
      border-radius: 12px 0px 0px 12px;
    }

    &--hight {
      border-radius: 0px 12px 12px 0px;
    }
  }
}

.period__meaning.no-active {
  background-color: var(--color-white);
  color: #030303;
}

.hidden {
  display: none !important;
}

.modals {
  opacity: 0;
  visibility: hidden;
  transition: opacity .4s ease-in-out, visibility .4s ease-in-out;
}

.modal-overlay {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  z-index: 1000;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  width: 100%;

  background: rgba(23, 73, 179, 0.1);
  backdrop-filter: blur(15px);

  opacity: 0;
  visibility: hidden;
  transition: opacity .5s ease-in-out, visibility .5s ease-in-out;
}

.modal {
  width: 100%;
  padding: 40px;

  position: relative;
  overflow-y: auto;
  overflow-x: hidden;

  background-color: var(--color-white);
  box-shadow: 0px 4px 24px rgba(213, 216, 221, 0.2);
  border-radius: 32px;

  &__title {
    font: var(--heading-3);
    color: var(--color-black-2);

    margin-bottom: 40px;
  }

  &__subtitle {
    font: var(--paragraph);
    color: var(--color-black);

    margin-bottom: 40px;
  }

  &__form {
    margin-bottom: 32px;

    &-field {
      display: flex;
      flex-direction: column;
      margin-bottom: 32px;

      &:last-child {
        margin-bottom: 0;
      }
    }

    &-label {
      font: var(--paragraph);
      color: #1b1b29;

      margin-bottom: 9px;
    }

    &-input {
      padding: 16px 24px;

      font: var(--paragraph);
      color: var(--color-blue);

      background-color: #f1f4f8;
      border: 1px solid #f1f4f8;
      border-radius: 12px;
      transition: border .2s ease-in-out;

      &::placeholder {
        font: var(--paragraph);
        color: #8f8f8f;
      }

      &:hover {
        transition: border .2s ease-in-out;
        border: 1px solid #838099;
      }
    }
  }

  &__btn {
    width: 100%;
    padding: 16px 40px;

    font: var(--button);
    color: var(--color-white);
    text-align: center;

    background-color: var(--color-blue);
    border: 1px solid var(--color-blue);
    border-radius: 12px;
  }

  &__question {
    font: var(--paragraph);
    color: var(--color-black);

    margin-bottom: 40px;
    
    &-btn {
      color: var(--color-blue);
      border-bottom: 1px solid var(--color-blue);
    }
  }

  &__sign {

    &-top {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 24px;
    }

    &-line {
      width: 100%;
      max-width: 160px;

      border: 1px solid #dfdfdf;
    }

    &-caption {
      font: var(--paragraph);
      color: #919191;
    }

    &-list {
      display: flex;
    }

    &-item {
      margin-right: 8px;

      &:last-child {
        margin-right: 0;
      }
    }

    &-social {
      display: flex;
      align-items: center;
      height: 70px;
      padding: 19px 58px;

      border: 1px solid #DFDFDF;
      border-radius: 12px;
      transition: background-color .2s ease-in-out, border .2s ease-in-out;

      &:hover {
        transition: background-color .2s ease-in-out, border .2s ease-in-out;
        background-color: #ECF3FB;
        border: 1px solid #ECF3FB;
      }
    }
  }

  &__privacy {
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;
    letter-spacing: 0.01em;

    max-width: 428px;

    color: #9c99ad;

    &-link {
      font-size: 15px;
      color: var(--color-blue);

      &--black {
        color: var(--color-black);
      }
    }
  }
}

.modals--visible {
  transition: opacity .4s ease-in-out, visibility .4s ease-in-out;
  opacity: 1;
  visibility: visible;
}

.modal-overlay--visible {
  transition: opacity .5s ease-in-out, visibility .5s ease-in-out;
  opacity: 1;
  visibility: visible;
}

.modal-close {
  position: absolute;
  right: 40px;
  top: 40px;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;

  background: rgba(23, 23, 23, 0.1);
  border-radius: 100px;
}

.modal-list {
  margin-bottom: 40px;

  &__label {
    font: var(--paragraph);
    color: var(--color-black-1);

    margin-bottom: 12px;
  }

  &__wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 11px 27px 11px 24px;

    position: relative;

    background-color: #f1f4f8;
    border-radius: 12px;
  }

  &__item {
    display: inline-block;
    font: var(--paragraph-blog);
    color: #8f8f8f;

    margin-right: 15px;
  }

  &__btn {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 32px;
    height: 32px;

    background-color: var(--color-white);
    border-radius: 8px;
  }
}


// media

@media (max-width: 768px) {
  .period {

    &__meaning {
      padding: 18px 20px;
    }
  }

  .page-title {
    font-size: 28px;
  }

  .page-subtitle {
    margin-bottom: 40px;
  }
}

@media (max-width: 375px) {
  .subscriptions-title {
    text-align: center;
  }

  .subscriptions-subtitle {
    text-align: center;
  }
}