.about {
  padding: var(--main-offset) 0;

  &__wrapper {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 16px;
  }

  &__info {
    padding: 60px;

    background-color: var(--color-white);
    box-shadow: 0px 4px 24px rgba(213, 216, 221, 0.2);
    border-radius: 32px;

    &-content {
      margin-bottom: 32px;
    }

    &-text {
      font: var(--paragraph-blog);
      color: var(--color-black-1);

      max-width: 470px;
      margin-bottom: 32px;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  &__favicon {
    margin-bottom: 32px;
  }

  &__quote {
    font: var(--button);
    color: var(--color-black-2);
  }

  &__picture {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: var(--color-white);
    box-shadow: 0px 12px 28px rgba(244, 245, 245, 0.2);
    border-radius: 32px;
  }
}


// media

@media (max-width: 968px) {
  .about {

    &__favicon {
      width: 60px;
      height: 60px;
    }
  }
}

@media (max-width: 768px) {
  .about {

    &__wrapper {
      grid-template-columns: repeat(1, 1fr);
    }

    &__info {
      padding: 20px;

      &-text {
        max-width: none;
      }
    }

    &__picture {
      
      img {
        max-height: 412px;
      }
    }
  }
}