.request {
  padding-bottom: 120px;

  &__wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 40px 60px;

    background-color: var(--color-blue);
    box-shadow: 0px 4px 24px rgba(213, 216, 221, 0.2), 0px 4px 24px rgba(213, 216, 221, 0.2), 0px 12px 28px rgba(244, 245, 245, 0.2);
    border-radius: 32px;    
  }

  &__info {
    margin-right: 20px;
  }

  &__text {
    font: var(--heading-4);
    color: var(--color-white);

    margin-bottom: 21px;
  }

  &__additional {
    font: var(--paragraph-blog);
    color: var(--color-white);
  }

  &__btn {
    font: var(--button);
    color: var(--color-black-2);

    padding: 16px 113px;

    background-color: var(--color-white);
    border-radius: 16px;
    transition: background-color .2s ease-in-out, color .2s ease-in-out, border .1s linear;

    &:hover {
      transition: background-color .2s ease-in-out, color .2s ease-in-out, border .1s linear;
      background-color: transparent;
      color: var(--color-white);
      border: 1px solid var(--color-white);
    }
  }
}

// media

@media (max-width: 768px) {
  .request {

    &__wrapper {
      flex-direction: column;
      padding: 40px 20px;
    }

    &__info {
      margin-right: 0;
      margin-bottom: 32px;
    }

    &__text {
      margin-bottom: 16px;
    }

    &__btn {
      width: 100%;
    }
  }
}

@media (max-width: 375px) {
  .request {

    &__btn {
      padding: 16px 30px;
    }
  }
}