.email-verif {

  &__window {
    max-width: 540px;
    padding-top: 60px;
  }

  &__title {
    margin-bottom: 12px;
  }

  &__request {
    max-width: 460px;
    margin-bottom: 40px;

    font: var(--paragraph);
    color: var(--color-black);
  }

  &__form {

    &-field {
      position: relative;
    }
  }

  &__resent {
    position: absolute;
    right: 23px;
    bottom: 20px;
    z-index: 5;

    font: var(--button);
    color: var(--color-blue);
  }

  &__sign {
    margin-bottom: 28px;
  }

  &__notreceive {
    font: var(--button);
    color: var(--color-blue);
  }
}