.crypto-pur {
  opacity: 0;
  
  &__window {
    max-width: 580px;
  }

  &__info {
    margin-bottom: 28px;

    &-item {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 24px;

      &:last-child {
        margin-bottom: 0;
      }
    }

    &-descr {
      font: var(--paragraph);
      color: var(--color-black-2);

      margin-right: 20px;
    }

    &-data {
      font: var(--paragraph);
      color: var(--color-black-2);
    }
  }

  &__remark {
    font: var(--paragraph);
    color: var(--color-black-2);
    
    margin-bottom: 12px;
  }

  &__address {
    display: flex;
    align-items: center;

    &-link {
      font: var(--paragraph);
      color: var(--color-blue);

      margin-right: 12px;

      &:hover {
        text-decoration: underline;
      }
    }
  }
}