.login {
  
  &__window {
    max-width: 540px;
  }

  &__title {
    margin-bottom: 12px;
  }

  &__subform {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 40px;

    &-forfot {
      font: var(--button);
      color: #0f0f0f;;
    }

    &-btn {
      padding: 15px 46px;

      font: var(--button);
      color: var(--color-white);

      background-color: var(--color-blue);
      border: 1px solid var(--color-blue);
      border-radius: 12px;
    }
  }

  &__sign {
    margin-bottom: 48px;
  }
}