.header {
  padding: 2px 0;
  background-color: var(--color-white);

  &__container {
    align-items: center;
    justify-content: space-between;
  }

  &__logo {
    align-items: center;

    &-link {
      margin-right: 8px;
    }

    &-text {
      font-family: 'Manrope', sans-serif;
      font-weight: 600;
      font-size: 16px;
    }
  }

  &__right {
    align-items: center;
  }

  &__social {
    align-items: center;
    margin-right: 40px;

    &-link {
      margin-right: 12px;

      &:last-child {
        margin-right: 0;
      }
    }
  }

  &__translate {
    align-items: center;
    margin-right: 40px;

    &-lang {
      font-weight: 500;
      font-size: 16px;
      text-transform: uppercase;
      margin-right: 4px;
    }

    &-btn {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 12px;
      height: 12px;
    }
  }

  &__login {
    font: var(--button);
    color: var(--color-white);
    padding: 9px 41px;

    background-color: var(--color-blue);
    border: 1px solid transparent;
    border-radius: 12px;
    transition: background-color .2s ease-in-out, color .2s ease-in-out, border .1s linear;
  }
}

.nav {

  &__list {
    align-items: center;
  }

  &__item {
    margin-right: 46px;

    &:last-child {
      margin-right: 0;
    }

    &--first {
      position: relative;
      align-items: center;
    }

    &-btn {
      width: 12px;
      height: 12px;
    }
  }

  &__link {
    font: var(--paragraph);
    color: var(--color-black-1);

    &--first {
      margin-right: 11px;
    }

    &:hover {
      color: var(--color-black);
    }
  }

  &__droplist {
    position: absolute;
    left: -30px;
    top: 30px;
    z-index: 100;

    width: 205px;
    padding: 27px 20px 24px 23px;

    background-color: var(--color-white);
    box-shadow: 0px 20px 48px rgba(177, 177, 177, 0.4);
    border-radius: 16px;

    opacity: 0;
    visibility: hidden;
    transition: opacity .1s ease-in-out, visibility .1s ease-in-out;

    &-item {
      margin-bottom: 20px;

      &:last-child {
        margin-bottom: 0;
      }
    }

    &-link {
      font: var(--button);
      color: var(--color-black-2);
      letter-spacing: 0.01em;

      &:hover {
        color: var(--color-blue);
      }
    }
  }
}

.nav__item--first:hover .nav__droplist {
  transition: opacity .1s ease-in-out, visibility .1s ease-in-out;
  display: block;
  opacity: 1;
  visibility: visible;
}

.translate {
  position: relative;

  &__dropmenu {
    position: absolute;
    left: 0;
    top: 30px;

    z-index: 100;

    padding: 24px;

    background-color: var(--color-white);
    box-shadow: 0px 20px 48px rgba(177, 177, 177, 0.4);
    border-radius: 16px;

    opacity: 0;
    visibility: hidden;
    transition: opacity .1s ease-in-out, visibility .1s ease-in-out;

    &-item {
      margin-bottom: 20px;

      &:last-child {
        margin-bottom: 0;
      }
    }

    &-lang {
      font: var(--button);
      color: var(--color-black-2);

      &:hover {
        color: var(--color-blue);
      }
    }
  }
}

.translate:hover .translate__dropmenu {
  transition: opacity .1s ease-in-out, visibility .1s ease-in-out;
  display: block;
  opacity: 1;
  visibility: visible;
}

.burger {
  width: 24px;
  height: 24px;

  position: relative;

  &__line {
    position: absolute;
    z-index: 100;
    left: 0;
    top: 50%;
    transform: translateY(-50%);

    display: block;
    width: 100%;
    height: 2px;

    background-color: #070033;
  }

  &::after {
    content: "";
    position: absolute;
    z-index: 100;
    left: 0;
    top: 4px;

    display: block;
    width: 100%;
    height: 2px;

    background-color: #070033;
  }

  &::before {
    content: "";
    position: absolute;
    z-index: 100;
    left: 0;
    bottom: 4px;

    display: block;
    width: 100%;
    height: 2px;

    background-color: #070033;
  }
}

.mobile {
  display: none;
}


// media

@media (max-width: 1024px) {
  .header {

    &__social{
      display: none;
    }
  }
}

@media (max-width: 968px) {
  .header {

    &__right {
      display: none;
    }
  }

  .nav {
    display: none;
  }

  .mobile {
    display: block;
  }
}

@media (max-width: 375px) {
  .header {

    &__logo {

      &-link {
        margin-right: 0;
      }

      &-text {
        display: none;
      }
    }
  }
}