.choice {
  padding: var(--main-offset) 0;

  &__left {
    padding: 60px;
  }

  &__card {
    align-items: center;
    justify-content: space-between;
    margin-bottom: 16px;

    background-color: var(--color-white);
    box-shadow: 0px 4px 24px rgba(213, 216, 221, 0.2);
    border-radius: 32px;

    &:last-child {
      margin-bottom: 0;
    }

    &-title {
      font: var(--heading-3);
      color: var(--color-black-2);

      margin-bottom: 22px;
    }

    &-descr {
      font: var(--paragraph);
      color: var(--color-black-1);

      max-width: 440px;
      margin-bottom: 38px;
    }

    &-more {
      display: inline-block;
      padding: 16px 24px;

      font: var(--button);
      color: var(--color-white);
      text-align: center;
      
      transition: background-color .2s ease-in-out, color .2s ease-in-out, border .1s linear;
      background-color: var(--color-blue);
      border: 1px solid var(--color-blue);
      border-radius: 16px;
    }
  }

  &__right {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    max-width: 588px;
    height: 342px;

    border-radius: 32px;
    
    &--netflix {
      background-color: var(--color-black);
    }

    &--youtube {
      background-color: #e1dceb;
    }
  }
}

// media

@media (max-width: 1100px) {
  .choice {

    &__right {
      max-width: 488px;
    }
  }
} 

@media (max-width: 1024px) {
  .choice {
    
    &__card {
      height: 200px;

      &-title {
        margin-bottom: 24px;
      }

      &-descr {
        display: none;
      }
    }

    &__right {
      max-width: 344px;
      height: 200px;

      overflow: hidden;
    }
  }
}

@media (max-width: 768px) {
  .choice {

    &__card {
      flex-direction: column-reverse;
      height: auto;

      &-descr {
        display: inline-block;
        max-width: none;
      }

      &-more {
        width: 100%;
      }
    }

    &__left {
      display: flex;
      flex-direction: column;
      align-items: center;
      padding: 20px;
    }

    &__right {
      height: 200px;
    }
  }
}

@media (max-width: 520px) {
  .choice {

    &__left {
      align-items: flex-start;
    }

    &__right {

      &--spotify {
        background-color: rgb(30, 215, 96);
      }
    }
  }
}