.faq {
  padding-top: 60px;
  padding-bottom: 120px;

  &__container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  &__list {
    width: 100%;
    margin-bottom: 40px;
  }

  &__item {
    margin-bottom: 16px;
    padding: 32px 32px 32px 40px;

    background-color: var(--color-white);
    box-shadow: 0px 12px 28px rgba(244, 245, 245, 0.2);
    border-radius: 32px;

    &:last-child {
      margin-bottom: 0;
    }

    &-wrapper {
      display: flex;
      align-items: flex-start;
      justify-content: space-between;
    }
  }

  &__question {
    font: var(--paragraph-blog);
    font-weight: 500;
    color: var(--color-black-2);

    margin-right: 15px;
  }

  &__btn {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    flex-shrink: 0;
    width: 40px;
    height: 40px;

    background: rgba(23, 23, 23, 0.05);
    border-radius: 12px;    
  }

  &__arrow {

    &--noactive {
      display: none;
    }
  }

  &__inner {
    display: none;
    margin-top: 20px;

    font: var(--paragraph-blog);
    color: var(--color-black-1);
  }

  &__support {
    font: var(--button);
    color: var(--color-white);
    padding: 16px 87px;

    background-color: var(--color-black-1);
    border: 1px solid var(--color-black-1);
    border-radius: 16px;
  }
}

.active {
  display: block;
}


// media

@media (max-width: 768px) {
  .faq {

    &__item {
      padding: 20px;
    }
  }
}