.hero {
  padding: var(--main-offset) 0;

  &__container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }

  &__left {
    margin-right: 16px;
    padding: 114px 60px;

    box-shadow: 0px 4px 24px rgba(213, 216, 221, 0.2);
    border-radius: 32px;
    background-color: var(--color-blue);
  }

  &__title {
    font: var(--heading-1);
    color: var(--color-white);

    max-width: 472px;
    margin-bottom: 24px;
  }

  &__descr {
    font: var(--paragraph);
    color: var(--color-white);

    max-width: 478px;
    margin-bottom: 40px;
  }

  &__btn {
    display: block;
    padding: 16px 57px;

    font: var(--button);
    color: var(--color-black-2);

    background-color: var(--color-white);
    border: 1px solid var(--color-white);
    border-radius: 16px;
    transition: background-color .2s ease-in-out, color .2s ease-in-out;

    &:hover {
      transition: background-color .2s ease-in-out, color .2s ease-in-out;
      background-color: transparent;
      color: var(--color-white);
    }
  }

  &__right {
    background-color: var(--color-white);
    box-shadow: 0px 12px 28px rgba(244, 245, 245, 0.2);
    border-radius: 32px;

    &-preview {
      height: 100%;
      border-radius: 32px;
    }
  }
}


// media

@media (max-width: 1024px) {
  .hero {

    &__left {
      padding: 30px;
    }
  }
}

@media (max-width: 768px) {
  .hero {

    &__container {
      display: flex;
      flex-direction: column-reverse;
    }

    &__left {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      margin-right: 0;
    }

    &__title {
     text-align: center;
    }

    &__descr {
      text-align: center;
    }

    &__right {
      margin-bottom: 16px;
    }
  }
}

@media (max-width: 520px) {
  .hero {

    &__left {
      padding: 20px;
    }

    &__title {
      text-align: left;
    }

    &__descr {
      text-align: left;
    }
  }
}