:root {
  --content-width: 1200px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --main-offset: 60px;

  // colors
  --color-white: #fff;
  --color-water: #ecf3fb;
  --color-black: #000;
  --color-black-1: #333;
  --color-black-2: #171717;
  --color-blue: #1749b3;
  --color-red: #fe0000;

  // fonts-desktop
  --font-family: "GothamPro", sans-serif;
  --heading-1: 900 36px / 140% var(--font-family);
  --heading-2: 900 32px / 140% var(--font-family); 
  --heading-3: 500 26px / 140% var(--font-family); 
  --heading-4: 700 22px / 140% var(--font-family); 
  --heading-5: 400 20px / 140% var(--font-family); 
  --button: 700 17px / 130% var(--font-family);
  --paragraph-blog: 400 18px / 150% var(--font-family);
  --paragraph: 400 16px / 150% var(--font-family);
  --label-1: 900 15px / 130% var(--font-family);
  --label-2: 700 14px / 130% var(--font-family);

  // font mobile

  @media (max-width: 768px) {
    --main-offset: 50px;
    --heading-1: 900 30px / 140% var(--font-family);
    --heading-2: 900 25px / 140% var(--font-family); 
    --heading-3: 500 22px / 140% var(--font-family); 
    --heading-4: 700 19px / 140% var(--font-family); 
    --heading-5: 400 18px / 140% var(--font-family); 
    --button: 700 15px / 130% var(--font-family);
    --paragraph-blog: 400 17px / 150% var(--font-family);
    --paragraph: 400 16px / 150% var(--font-family);
  }
}
