.logout {

  &__window {
    max-width: 540px;
  }

  &__btns {
    display: flex;

    &-cancel {
      font: var(--button);
      color: var(--color-blue);

      margin-right: 8px;
      padding: 16px 77px;
      border-radius: 12px;
      border: 1px solid var(--color-blue);

      &:hover {
        background-color: var(--color-blue);
        color: var(--color-white);
      }
    }

    &-agree {
      font: var(--button);
      color: var(--color-white);

      width: 100%;

      background-color: var(--color-blue);
      border-radius: 12px;
      border: 1px solid var(--color-blue);
    }
  }
}