.forget-pass-new {
  opacity: 0;

  &__window {
    max-width: 540px;
    padding-top: 84px;
  }

  &__title {
    margin-bottom: 12px;
  }

  &__form {
    display: flex;

    &-field {
      max-width: 220px;
      margin-right: 20px;
      margin-bottom: 0;

      &:last-child {
        margin-right: 0;
      }
    }
  }

  &__restore {
    margin-bottom: 44px;
  }
}