.profile {
  display: flex;
  align-items: center;

  &__avatar {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 32px;
    height: 32px;
    margin-right: 12px;

    background-color: var(--color-blue);
    border-radius: 100%;
  }

  &__text {
    font: var(--paragraph);
    color: var(--color-black-2);

    margin-right: 4px;
  }

  &__arrow {
    display: inline-block;
    width: 12px;
    height: 12px;
    margin-top: 2px;

    background-image: url('../img/arrow-down.svg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
  }
}