.account {
  padding: var(--main-offset) 0;
  padding-bottom: 120px;

  &__top {
    align-items: center;
    justify-content: space-between;
    margin-bottom: 60px;
  }

  &__title {
    margin-bottom: 0;
  }

  &__exit {
    display: flex;
    align-items: center;

    &-ico {
      margin-right: 12px;
    }

    &-caption {
      font: var(--heading-5);
      color: var(--color-black-1);
    }
  }

  &__content {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    margin-bottom: 60px;
  }

  &__menu {
    width: 350px;
    margin-right: 44px;
    padding: 15px 16px;

    background-color: var(--color-white);
    box-shadow: 0px 12px 28px rgba(177, 177, 177, 0.2);
    border-radius: 12px;    
  }

  &__item {
    display: flex;
    align-items: center;  
    padding: 11px 20px;

    &:hover {
      cursor: pointer;
      background-color: #f7f7f7;
    }

    &-img {
      margin-right: 12px;
    }
  }

  &__info {
    display: none;
    width: 100%;
    max-width: 806px;
    padding: 40px;

    background-color: var(--color-white);
    box-shadow: 0px 12px 28px rgba(244, 245, 245, 0.2);
    border-radius: 32px;

    &-title {
      font: var(--heading-4);
      color: #0d0b10;

      margin-bottom: 40px;
    }

    &-save {
      width: 100%;
      padding: 15px 0;

      font: var(--button);
      color: var(--color-white);

      background-color: var(--color-blue);
      border: 1px solid var(--color-blue);
      border-radius: 12px;
    }
  }

  &__input {
    display: flex;
    flex-direction: column;
    margin-bottom: 32px;

    &:last-child {
      margin-bottom: 0;
    }

    &-label {
      font: var(--paragraph);
      color: #1b1b29;

      margin-bottom: 12px;
    }

    &-field {
      padding: 0;
      padding: 16px 24px;

      font: var(--paragraph);
      color: var(--color-blue);
      
      background-color: #f1f4f8;
      border: 1px solid #f1f4f8;
      border-radius: 8px;

      &::placeholder {
        font: var(--paragraph);
        color: #8f8f8f;
      }

      &:hover {
        border: 1px solid #838099;
      }
    }
  }

  &__sub {
    display: none;
    width: 100%;
    max-width: 806px;

    box-shadow: 0px 12px 28px rgba(244, 245, 245, 0.2);

    &-wrapper {
      margin-bottom: 16px;

      &:last-child {
        margin-bottom: 0;
      }
    }

    &-top {
      padding: 69px 40px;

      background: #1ed760;
      border-radius: 32px 32px 0px 0px;

      &--youtube {
        background-color: var(--color-red);
      }
    }

    &-title {
      font: var(--heading-2);
      color: var(--color-white);
    }

    &-info {
      padding: 32px 40px;

      background-color: var(--color-white);
      border-radius: 0 0 32px 32px;
    }

    &-descr {
      font: var(--paragraph-blog);
      color: #232323;

      margin-bottom: 24px;
    }

    &-plan {
      font: var(--button);
      color: var(--color-black-1);

      border-bottom: 1px solid var(--color-black-1);
    }
  }
}

.tab-active {
  display: block;
}

// media

@media (max-width: 768px) {
  .account {

    &__content {
      flex-direction: column;
    }

    &__menu {
      width: 100%;
      margin-right: 0;
      margin-bottom: 24px;
    }

    &__exit {
      display: none;
    }
  }
}