.footer {
  padding-top: 40px;
  padding-bottom: 12px;

  background-color: var(--color-blue);

  &__column {
    display: flex;
    justify-content: space-between;

    margin-bottom: 40px;
  }

  &__logo {
    display: flex;
    align-items: center;
    margin-bottom: 23px;

    &-link {
      width: 55px;
      height: 55px;
      margin-right: 15px;
    }

    &-caption {
      font: var(--paragraph);
      color: var(--color-white);
    }
  }

  &__quote {
    font: var(--paragraph);
    color: var(--color-white);

    max-width: 180px;
  }

  &__nav {

    &-item {
      margin-bottom: 12px;

      &:first-child {
        margin-bottom: 20px;
      }

      &:last-child {
        margin-bottom: 0;
      }
    }

    &-title {
      font: var(--paragraph-blog);
      color: var(--color-white);
    }

    &-link {
      font: var(--paragraph);
      color: var(--color-white);

      &:hover {
        text-decoration: underline;
      }
    }
  }

  &__right {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
  }

  &__messager {
    display: flex;
    align-items: center;
    margin-bottom: 17px;

    &-link {
      width: 40px;
      height: 40px;
      margin-right: 16px;

      &:last-child {
        margin-right: 0;
      }
    }
  }

  &__translate {
    display: flex;
    align-items: center;

    &-lang {
      display: inline-block;
      font: var(--button);
      color: var(--color-white);
      text-transform: uppercase;

      margin-right: 3px;
    }

    &-btn {
      display: flex;
      width: 12px;
      height: 12px;
    }

    &-dropmenu {
      width: 114px;
      margin-left: -120px;
      left: 0 !important;
      top: 0 !important;
    }
  }

  &__bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__privacy {
    font: var(--paragraph);
    color: #d3d3d3;
  }

  &__copyright {
    font: var(--paragraph);
    color: #d3d3d3;
  }

  &__developed {
    font: var(--paragraph);
    color: #d3d3d3;
  }
}


// media

@media (max-width: 768px) {
  .footer {

    &__column {
      flex-direction: column;
    }

    &__left {
      margin-bottom: 40px;
    }

    &__nav {
      margin-bottom: 40px;
    }

    &__right {
      flex-direction: row;
      align-items: center;
      justify-content: space-between;
    }

    &__messager {
      margin-bottom: 0;
    }

    &__bottom {
      flex-direction: column;
      padding-top: 18px;
      border-top: 1px solid var(--color-white);
    }

    &__privacy {
      margin-bottom: 12px;
      font-size: 15px;
    }

    &__copyright {
      margin-bottom: 12px;

      font-size: 15px;
    }

    &__developed {
      font-size: 15px;
    }
  }
}